import { MessageCenterCoreComponent } from './message-center-core/message-center-core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: '',
    component: MessageCenterCoreComponent,
    data: {title: 'Message Center', breadcrumb: 'Message-Center'}
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MessageCenterRoutingModule { }
