import { Component, OnInit, ViewChild } from '@angular/core';
import { GlobalService } from '../../../../shared/services/global.service';
import { ModalDirective } from 'ngx-bootstrap/modal';


@Component({
  selector: 'app-message-center-core',
  templateUrl: './message-center-core.component.html',
  styleUrls: ['./message-center-core.component.scss']
})
export class MessageCenterCoreComponent implements OnInit {

  messages: any[] = [];
  imgsrc;
  msgparagraph;
  public order = ''; // Order property to sort coloum of table
  public reverse = true; // Reverse property is used to sort table coloum
  public pagination;
  public Items = 1;
  public TotalCount = 0;
  data;
  searchText;
  categoryOption;
  constructor(private global: GlobalService) {

   }

  @ViewChild('autoShownModal', { static: false }) autoShownModal: ModalDirective;
  isModalShown = false;

  showModal(img,paragraph): void {
    this.imgsrc = img;
    this.msgparagraph = paragraph;
    this.isModalShown = true;
  }

  hideModal(): void {
    this.autoShownModal.hide();
  }

  onHidden(): void {
    this.isModalShown = false;
  }

  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }
    this.order = value;
  }

  getMessage(pn, category){

this.categoryOption = category;
if (category) {
      this.pagination = pn;
      this.data = {
        Profile: this.global.localDealermasterID,
        Initial: 1,
        MaxCount: pn
      };
    } else {
      this.pagination = pn;
      this.data = {
        Profile: this.global.localDealermasterID,
        Initial: (this.pagination - 1) * this.Items + 1,
        MaxCount: this.pagination * this.Items
      };
    }

this.global.getAdmmsg(this.data).subscribe(res =>{

      this.messages = res.lstMsgs;
      this.TotalCount = res.Count;

    }, err => {
      console.log(err);
    });
  }

  ngOnInit(): void {
    this.getMessage(1,this.categoryOption);
  }

}
